.main_nav{
    padding: 1rem  ;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid white;
    background-color: black;
    position: fixed;
   width:100%
   
}


.menu-link {
    display:flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;    
    
    margin-right: 3rem;
}

.menu-link>li{
    padding: 0 1rem;
}

.menu-link>li>a{
    font-size: 1.2rem;
    font-weight: 500;
}


.hamburger{
    display: none;
    
}

@media screen and (max-width : 1040px){

 .menu-link{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height:100vh;
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.3s;

 }
 
.menu-link.active{
    left: 0;
    background-color: black;
    
    
 }

 .menu-link>li{
    padding: 1rem;
    }
    

 .menu-link>li>a{
    font-size: 2rem;
 } 

 

     /* .menu-link>li{
        display:none
    }  */
     .hamburger{
         margin-right: 3rem;
        display: block;
       
      
    } 
    /* .hamburger>a{
        color: white;
    } */

}